/* Importing reset.css file */
@import '../reset.css';

/* Media query for screens between 768px and 1024px */
@media screen and (min-width: 768px) and (max-width: 1024px) {
  .header_main_name .header__img {
    display: none;
  }

  .header_main_name .header__span {
    grid-column: 1 / span 2;
  }

  .sidebar.mobile {
    display: flex;
    flex-direction: column;
  }

  .sidebar.mobile .select__menu.mobile {
    flex-direction: row;
    justify-content: space-around;
    align-items: flex-start;
  }

  .sidebar.mobile .select__menu.mobile .h2__title {
    padding-top: 8px;
  }

  .sidebar.mobile .select__menu.mobile .ages {
    margin-bottom: 8px;
  }

  .gif__description .physical__list {
    flex-direction: column;
  }
}

/* Media query for screens up to 767px */
@media screen and (max-width: 767px) {
  .header_main_name .header__img {
    display: none;
  }

  .header_main_name .header__span {
    grid-column: 1 / span 2;
  }

  .sidebar.mobile {
    display: flex;
    flex-direction: column;
  }

  .sidebar.mobile .select__menu.mobile {
    flex-direction: column;
    justify-content: space-around;
    align-items: center;
    gap: 0;
  }

  .sidebar.mobile .select__menu.mobile .h2__title {
    padding-top: 8px;
  }

  .sidebar.mobile .select__menu.mobile .ages {
    margin-bottom: 8px;
  }

  .gif__description .physical__list {
    flex-direction: column;
  }
}

/* Common styles for both media queries */
.container {
  height: 100vh;
  display: grid;
  grid-template-rows: 1fr 10fr;
}

.clicked {
  color: #d9ff00;
}

.header_main_name {
  background-color: #fff;
  display: grid;
  grid-template-columns: 0.5fr 3.5fr;
  font-weight: 600;
  align-items: center;
}

.header_main_name .header__span {
  display: flex;
  justify-content: center;
  font-size: 50px;
  text-align: center;
}

.header_main_name .header__img {
  width: 100%;
  padding-left: 8px;
  object-fit: cover;
}

.sidebar {
  display: grid;
  grid-template-columns: 1.5fr 10fr;
  box-shadow: 0px 5px 20px 0px #898888;
}

.sidebar .select__menu {
  display: flex;
  flex-direction: column;
  gap: 10px;
  align-items: center;
  background-color: #083e58;
  color: #fff;
}

.sidebar .select__menu .h2__title {
  font-size: 30px;
  padding-top: 14px;
}

.sidebar .select__menu .ages {
  margin-top: 10px;
  display: flex;
  flex-direction: column;
  align-items: center;
  text-align: center;
  font-size: 22px;
}

.sidebar .select__menu .ages .physical__list {
  display: flex;
  flex-direction: column;
  gap: 5px;
  font-size: 18px;
}

.sidebar .select__menu .ages .physical__list li:first-child {
  margin-top: 10px;
}

.sidebar .select__menu .ages:first-child {
  margin-top: 50px;
}

.sidebar .select__menu .ages .custom-div {
  width: 150px;
  height: 40px;
  border-radius: 5px;
  cursor: pointer;
  transition: all 0.3s ease;
  position: relative;
  display: inline-block;
  box-shadow: inset 2px 2px 2px 0px rgba(255, 255, 255, 0.5),
    7px 7px 20px 0px rgba(0, 0, 0, 0.1), 4px 4px 5px 0px rgba(0, 0, 0, 0.1);
  outline: none;
}

.sidebar .select__menu .ages .div-8 {
  background-color: #f0ecfc;
  line-height: 42px;
  padding: 0;
  border: none;
  color: #000;
}

.sidebar .select__menu .ages .div-8 {
  background-color: #f0ecfc;
  line-height: 42px;
  padding: 0;
  border: none;
  color: #000;
}

.sidebar .select__menu .ages .div-8 span {
  position: relative;
  display: block;
  width: 100%;
  height: 100%;
}

.sidebar .select__menu .ages .div-8 span:hover {
  color: #c797eb;
}

.sidebar .select__menu .ages .div-8 span:before,
.sidebar .select__menu .ages .div-8 span:after {
  position: absolute;
  content: '';
  left: 0;
  top: 0;
  background: #c797eb;
  transition: all 0.3s ease;
}

.sidebar .select__menu .ages .div-8 span:before {
  width: 2px;
  height: 0%;
}

.sidebar .select__menu .ages .div-8 span:after {
  height: 2px;
  width: 0%;
}

.sidebar .select__menu .ages .div-8 span:hover:before {
  height: 100%;
}

.sidebar .select__menu .ages .div-8 span:hover:after {
  width: 100%;
}

.sidebar .select__menu .ages .div-8:hover {
  color: #fff;
  background: transparent;
}

.gif__description {
  padding: 40px 60px;
}

.gif__description .physical__list {
  box-shadow: 0px 10px 20px 10px #898888;
  width: 100%;
  height: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
  background-color: #000;
  gap: 3px;
  border: 3px solid #000;
  box-sizing: border-box;
}

.gif__description .physical__list-item {
  width: 100%;
  display: flex;
  flex-direction: column;
  align-items: center;
  height: 100%;
}

.gif__description .physical__list-item img {
  width: 100%;
  height: 30em;
  background-color: #fff;
  box-sizing: border-box;
  padding: 3px;
  max-height: 100%;
  object-fit: contain;
}

.gif__description .physical__list-item .des__box {
  display: grid;
  width: 100%;
  height: 100%;
  grid-template-columns: repeat(2, 1fr);
  grid-template-rows: repeat(3, 1fr);
  gap: 3px;
  padding-top: 3px;
}

.gif__description .physical__list-item .des__box div {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 100%;
  height: 100%;
  background-color: #fff;
  font-size: 20px;
  text-align: center;
}
